import React from 'react';
import { withScriptjs, withGoogleMap, GoogleMap, Marker, InfoWindow } from "react-google-maps";

const CovidMap = withScriptjs(withGoogleMap((props) => {
    const covidLocations = props.covidLocations;
    const userInfo = props.userInfo; //lat and long values of user address

    this.state = {
        selectedLoc: null,
        centre: [-33.873432, 151.206396],
    }

    return(
        <div style={{ height: '100vh', width: '100%' }}>
            <GoogleMap 
                zoom={5} 
                center={{lat:this.state.centre[0], long:this.state.centre[1]}}
                bootstrapURLKeys={{ key: 'put your API key here' }}
            >
                {
                    covidLocations.map(loc => (
                        <Marker
                            key={loc.id}
                            position = {{
                                lat: loc.Lat,
                                lng: loc.Lon
                            }}

                            onClick={() => {
                                this.setState({selectedLoc: loc});
                            }}
                        />
                        
                    ))
                };

                {this.state.selectedLoc && (
                    <InfoWindow
                        onCloseClick={()=>{
                            this.state.selectedLoc = null;
                        }}
                        position={{
                            lat: this.state.selectedLoc.Lat,
                            lng: this.state.selectedLoc.Lon
                        }}
                    >
                        <div>
                            <h3>{this.state.selectedLoc.Venue}</h3>
                            <h4>{"Alert: " + this.state.selectedLoc.Alert}</h4>
                            <h5>{"Suburb:" + this.state.selectedLoc.Suburb}</h5>
                            <h5>{this.state.selectedLoc.Date}</h5>
                            <h5>{this.state.selectedLoc.Time}</h5>
                        </div>
                                            
                    </InfoWindow>
                )}

            </GoogleMap>
        </div>
    )
}))

export default CovidMap;