import React, {Component} from 'react'
import {Link} from 'react-router-dom';
import axios from 'axios';
import {Card }from 'react-bootstrap';


import CovidMap from './stats_map.component';

export default class CovidStats extends Component {
    constructor(props){

        this.state = {
            
            userInfo: props.userInfo,
            userLocations: props.userLocations, //list of locations visited by the user
            cases: []
        }
    };

    async componentDidMount(){

        await axios.get('https://data.nsw.gov.au/data/dataset/0a52e6c1-bc0b-48af-8b45-d791a6d8e289/resource/f3a28eed-8c2a-437b-8ac1-2dab3cf760f9/download/venue-data.json')
            .then(response => {
                this.setState({cases: response.data})
            })
            .catch((error) => {
                console.log(error)
            })
    }

    render(){

        return(
            <div>
                
                <CovidMap covidLocations={this.state.cases} userInfo={this.state.user}/>
                <div className="container-fluid">
                    <div className="col-sm-12 btn btn-info" style={{marginBottom: "20px"}}>
                        <h3>Recent Cases</h3>   
                    </div>
                </div>
                <div className="container-fluid">

                    <div className="d-flex flex-row flex-nowrap">
                        <Card><Card.Body>{this.state.cases[0]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[1]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[2]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[3]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[4]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[5]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[6]['Venue']}</Card.Body></Card>
                        <Card><Card.Body>{this.state.cases[7]['Venue']}</Card.Body></Card>
                    </div>

                </div>
            </div>
        );
    }
}

